import { api } from "src/reusables/api";
import {Product, ProductDetails} from "./types";
import {BASE_URL} from "../index";

export const productsRepository = {
  getProducts: (): Promise<Product[]> => api.get(BASE_URL + `/products`),
  getProductById: (productId: Product['productId']): Promise<ProductDetails> => api.get(BASE_URL + `/patterns/${productId}`),
};
