import React from "react";
import {Collapse, Typography} from "antd";
import {PageHeader} from "../../reusables/PageHeader";
import {ContentPage} from "../../reusables/ContentPage";

import './styles.less';

export const Questions: React.FC = () => {
  return (
    <ContentPage>
      <PageHeader>
        <Typography.Title>
          FAQ
        </Typography.Title>
      </PageHeader>
      <div className="questions-content">
        <Collapse>
          <Collapse.Panel header="Как удалить схему?" key={1}>
            <p>
              Чтобы удалить схему, проведите пальцем влево на схеме и нажмите на значок корзины
            </p>
          </Collapse.Panel>
          <Collapse.Panel header="Как редактировать схему?" key={2}>
            <p>
              Эта функция пока недоступна, но планируется
            </p>
          </Collapse.Panel>
          <Collapse.Panel header="Что такое номер канвы?" key={3}>
            <p>
              Номер канвы определяется количеством квадратов на дюйм. Номер канвы влияет на окончательнй
              размер дизайна и количество нитей, необходимых для вышивки.
            </p>
          </Collapse.Panel>
        </Collapse>
      </div>
    </ContentPage>
  );
};
