import React from "react";
import {Col, Row, Typography} from "antd";
import {PageHeader} from "../../reusables/PageHeader";
import {ContentPage} from "../../reusables/ContentPage";

import './styles.less';
import {Form, Input, SubmitButton } from "formik-antd";
import { Formik } from "formik";

export const Comment: React.FC = () => {
  return (
    <ContentPage>
      <PageHeader>
        <Typography.Title>
          Отзывы
        </Typography.Title>
      </PageHeader>
      <div style={{ width: 500 }}>
        <Formik
          initialValues={{
            name: '',
            theme: '',
            comment: '',
          }}
          onSubmit={() => undefined}
        >
          <Form
            name="comment"
            autoComplete="off"
            layout="vertical"
          >
            <Form.Item label="Email" name="name">
              <Input name="name" />
            </Form.Item>

            <Form.Item label="Тема" name="theme">
              <Input name="theme" />
            </Form.Item>

            <Form.Item name="comment" label="Отзыв">
              <Input.TextArea name="comment"  rows={5} />
            </Form.Item>

            <Row>
              <Col span={24} style={{ textAlign: 'right' }}>
                <SubmitButton>
                  Отправить
                </SubmitButton>
              </Col>
            </Row>
          </Form>
        </Formik>
      </div>
    </ContentPage>
  );
};
