import React from 'react';
import {Outlet, Route, Routes } from 'react-router-dom';
import { BrowserRouter } from 'react-router-dom';
import { MainPage } from "./stories/MainPage";
import { SchemesList } from './stories/SchemesList';
import { Pattern } from "./stories/Pattern";
import {Layout} from "./reusables/Layout";
import {Terms} from "./stories/Terms";
import { Policy } from './stories/Policy';
import {Questions} from "./stories/Questions";
import {Comment} from "./stories/Comment";

import 'normalize.css';
import './global.css';
import {Products} from "./stories/Products";

export const App: React.FC = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route
          element={
            <Layout>
              <Outlet />
            </Layout>
          }
        >
          <Route path={ROUTE_TO_MAIN_PAGE} element={<MainPage />} />
          <Route path={ROUTE_TO_SCHEME_LIST} element={<SchemesList />} />
          <Route path={ROUTE_TO_PATTERN} element={<Pattern />} />
          <Route path={ROUTE_TO_TERMS} element={<Terms />} />
          <Route path={ROUTE_TO_POLICY} element={<Policy />} />
          <Route path={ROUTE_TO_FAQ} element={<Questions />} />
          <Route path={ROUTE_TO_COMMENTS} element={<Comment />} />
          <Route path={ROUTE_TO_PRODUCTS} element={<Products />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
};

export const ROUTE_TO_MAIN_PAGE = '/';
export const ROUTE_TO_SCHEME_LIST = '/patterns';
export const ROUTE_TO_PATTERN = '/patterns/:patternId';
export const ROUTE_TO_TERMS = '/terms';
export const ROUTE_TO_POLICY = '/policy';
export const ROUTE_TO_FAQ = '/faq';
export const ROUTE_TO_COMMENTS = '/comments';
export const ROUTE_TO_PRODUCTS = '/products';
export const ROUTE_TO_PRODUCT = '/products/:id';
