import React from "react";
import { Product } from "src/externals/Products/types";
import { Space, Typography} from "antd";
import {NavLink} from "react-router-dom";
import {isNil} from "lodash-es";

import './styles.less';

export const ProductItem: React.FC<Props> = ({ productId, cost, title, image }) => {
  return (
    <NavLink className="product-item" to={`/products/${productId}`}>
      <div className="product-item--image">
        <img src={image} />
      </div>
      <div className="product-item--description">
        <Space direction="vertical">
          <Typography.Paragraph ellipsis>{ title }</Typography.Paragraph>

          { isNil(cost) && <Typography.Paragraph className="product-item--cost" strong>Бесплатно</Typography.Paragraph>}
          { !isNil(cost) && <Typography.Paragraph className="product-item--cost" strong>{ cost } P</Typography.Paragraph>}
        </Space>
      </div>
    </NavLink>
  );
};

type Props = Product;